'use server';

interface PurchaseTicketData {
  userId: string; // ✅ userId passé depuis le client
  ligneId: string;
  routeName: string;
  departureStation: string;
  arrivalStation: string;
  heureDepart?: string;
  heureArrivee?: string;
  paymentMethod?: string;
  paymentReference?: string;
}

interface TicketResponse {
  success: boolean;
  ticket?: {
    ticket_id: string;
    user_id: string;
    ligne_id: string;
    ticket_code: string;
    price: string;
    status: string;
    purchase_date: string;
    valid_until: string;
    route_name: string;
    departure_station: string;
    arrival_station: string;
    heure_depart?: string;
    heure_arrivee?: string;
    is_valid: boolean;
  };
  message: string;
}

interface TransactionData {
  ticket_id: string;
  user_id: string;
  action: string;
  amount: number;
  payment_method: string;
  payment_reference?: string;
}

interface TransactionResponse {
  success: boolean;
  transaction?: any;
  message: string;
}

/**
 * Action serveur pour acheter un ticket et créer la transaction
 */
export async function purchaseTicketWithTransaction(ticketData: PurchaseTicketData): Promise<{ 
  success: boolean; 
  message: string; 
  ticket?: any; 
  transaction?: any;
  ticketCode?: string;
}> {
  try {
    // Validation de l'userId
    if (!ticketData.userId) {
      return {
        success: false,
        message: 'ID utilisateur manquant'
      };
    }

    // ÉTAPE 1 : Création du ticket
    const ticketPayload = {
      user_id: ticketData.userId,
      ligne_id: ticketData.ligneId,
      route_name: ticketData.routeName,
      departure_station: ticketData.departureStation,
      arrival_station: ticketData.arrivalStation,
      heure_depart: ticketData.heureDepart || '',
      heure_arrivee: ticketData.heureArrivee || '',
      payment_method: ticketData.paymentMethod || 'CARD',
      payment_reference: ticketData.paymentReference || ''
    };

    const ticketResponse = await fetch('http://localhost:9000/api/tickets/purchase/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(ticketPayload)
    });

    const ticketResult: TicketResponse = await ticketResponse.json();

    if (!ticketResult.success || !ticketResult.ticket) {
      return {
        success: false,
        message: ticketResult.message || 'Erreur lors de la création du ticket'
      };
    }

    // ÉTAPE 2 : Création de la transaction avec l'ID du ticket
    const transactionPayload: TransactionData = {
      ticket_id: ticketResult.ticket.ticket_id,
      user_id: ticketData.userId,
      action: 'PURCHASE',
      amount: 5.00, // Prix fixe
      payment_method: ticketData.paymentMethod || 'CARD',
      payment_reference: ticketData.paymentReference || ''
    };

    const transactionResponse = await fetch('http://localhost:9000/api/transactions/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(transactionPayload)
    });

    const transactionResult: TransactionResponse = await transactionResponse.json();

    if (!transactionResult.success) {
      console.warn('Transaction échouée mais ticket créé:', ticketResult.ticket.ticket_code);
      
      return {
        success: false,
        message: 'Ticket créé mais erreur lors de la transaction: ' + transactionResult.message,
        ticket: ticketResult.ticket,
        ticketCode: ticketResult.ticket.ticket_code
      };
    }

    // SUCCÈS : Ticket et transaction créés
    return {
      success: true,
      message: 'Ticket et transaction créés avec succès',
      ticket: ticketResult.ticket,
      transaction: transactionResult.transaction,
      ticketCode: ticketResult.ticket.ticket_code
    };

  } catch (error) {
    console.error('Erreur achat ticket avec transaction:', error);
    return {
      success: false,
      message: 'Erreur de connexion au serveur: ' + (error instanceof Error ? error.message : 'Unknown error')
    };
  }
}

/**
 * Action pour valider un ticket
 */
export async function validateTicket(ticketCode: string): Promise<{ 
  success: boolean; 
  message: string; 
  ticket?: any;
}> {
  try {
    const response = await fetch('http://localhost:9000/api/tickets/validate/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        ticket_code: ticketCode
      })
    });

    const result: TicketResponse = await response.json();

    return {
      success: result.success,
      message: result.message,
      ticket: result.ticket
    };

  } catch (error) {
    console.error('Erreur validation ticket:', error);
    return {
      success: false,
      message: 'Erreur de connexion au serveur'
    };
  }
}

/**
 * Récupérer les tickets d'un utilisateur
 */
export async function getUserTickets(userId: string): Promise<{
  success: boolean;
  message: string;
  tickets?: any[];
}> {
  try {
    if (!userId) {
      return {
        success: false,
        message: 'ID utilisateur manquant',
        tickets: []
      };
    }

    const response = await fetch(`http://localhost:9000/api/tickets/?user_id=${userId}`);
    const result = await response.json();

    if (response.ok) {
      return {
        success: true,
        message: 'Tickets récupérés avec succès',
        tickets: result.tickets || result
      };
    } else {
      return {
        success: false,
        message: result.detail || 'Erreur lors de la récupération des tickets',
        tickets: []
      };
    }

  } catch (error) {
    console.error('Erreur récupération tickets:', error);
    return {
      success: false,
      message: 'Erreur de connexion au serveur',
      tickets: []
    };
  }
}